/**
 * 
 * 
 * 
 */

type QueueItem = {
  id: string;
  timestamp: number;
  endpoint: string;
  method: string;
  data: any;
  retryCount: number;
  lastAttempt?: number;
};

// 
const OFFLINE_QUEUE_KEY = 'echo_offline_queue';
const MAX_QUEUE_AGE_DAYS = 7; // 
const MAX_RETRY_COUNT = 5; // 

/**
 * 
 */
export const isOnline = (): boolean => {
  return navigator.onLine;
};

/**
 * 
 */
export const listenToConnectionChanges = (
  onOnline: () => void,
  onOffline: () => void
): (() => void) => {
  window.addEventListener('online', onOnline);
  window.addEventListener('offline', onOffline);
  
  // 
  return () => {
    window.removeEventListener('online', onOnline);
    window.removeEventListener('offline', onOffline);
  };
};

/**
 * 
 */
export const addToOfflineQueue = (
  endpoint: string,
  method: string,
  data: any
): string => {
  try {
    const queue = getOfflineQueue();
    
    // ID
    const id = `${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    
    // 
    const newItem: QueueItem = {
      id,
      timestamp: Date.now(),
      endpoint,
      method,
      data,
      retryCount: 0
    };
    
    // 
    queue.push(newItem);
    
    // 
    saveOfflineQueue(queue);
    
    console.info(`: ${method} ${endpoint}`);
    return id;
  } catch (error) {
    console.error(':', error);
    throw error;
  }
};

/**
 * 
 */
export const removeFromOfflineQueue = (id: string): boolean => {
  try {
    const queue = getOfflineQueue();
    const initialLength = queue.length;
    
    // ID
    const newQueue = queue.filter(item => item.id !== id);
    
    // 
    if (newQueue.length !== initialLength) {
      saveOfflineQueue(newQueue);
      return true;
    }
    
    return false;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};

/**
 * 
 */
export const getOfflineQueue = (): QueueItem[] => {
  try {
    const queueData = localStorage.getItem(OFFLINE_QUEUE_KEY);
    
    if (!queueData) {
      return [];
    }
    
    return JSON.parse(queueData);
  } catch (error) {
    console.error(':', error);
    return [];
  }
};

/**
 * 
 */
export const saveOfflineQueue = (queue: QueueItem[]): void => {
  try {
    // 
    const now = Date.now();
    const maxAge = MAX_QUEUE_AGE_DAYS * 24 * 60 * 60 * 1000; // 
    const filteredQueue = queue.filter(item => now - item.timestamp < maxAge);
    
    localStorage.setItem(OFFLINE_QUEUE_KEY, JSON.stringify(filteredQueue));
  } catch (error) {
    console.error(':', error);
  }
};

/**
 * 
 */
export const syncOfflineData = async (): Promise<{
  success: number;
  failed: number;
  remaining: number;
}> => {
  // 
  if (!isOnline()) {
    return { success: 0, failed: 0, remaining: getOfflineQueue().length };
  }
  
  const queue = getOfflineQueue();
  if (queue.length === 0) {
    return { success: 0, failed: 0, remaining: 0 };
  }
  
  console.info(`: ${queue.length}`);
  
  let successCount = 0;
  let failedCount = 0;
  
  // 
  for (const item of queue) {
    try {
      // 
      item.retryCount += 1;
      item.lastAttempt = Date.now();
      
      // 
      const response = await fetch(item.endpoint, {
        method: item.method,
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(item.data)
      });
      
      if (response.ok) {
        // 
        removeFromOfflineQueue(item.id);
        successCount++;
      } else {
        // API
        const errorData = await response.json();
        console.error(`: ${response.status}`, errorData);
        
        // 
        if (item.retryCount >= MAX_RETRY_COUNT) {
          removeFromOfflineQueue(item.id);
        }
        
        failedCount++;
      }
    } catch (error) {
      console.error(`:`, error);
      
      // 
      if (item.retryCount >= MAX_RETRY_COUNT) {
        removeFromOfflineQueue(item.id);
      }
      
      failedCount++;
    }
  }
  
  // 
  const remainingCount = getOfflineQueue().length;
  
  console.info(`: =${successCount}, =${failedCount}, =${remainingCount}`);
  
  return {
    success: successCount,
    failed: failedCount,
    remaining: remainingCount
  };
};

/**
 * 
 */
export const getOfflineQueueStatus = (): {
  count: number;
  oldestTimestamp: number | null;
  newestTimestamp: number | null;
} => {
  const queue = getOfflineQueue();
  
  if (queue.length === 0) {
    return {
      count: 0,
      oldestTimestamp: null,
      newestTimestamp: null
    };
  }
  
  // 
  const sortedQueue = [...queue].sort((a, b) => a.timestamp - b.timestamp);
  
  return {
    count: queue.length,
    oldestTimestamp: sortedQueue[0].timestamp,
    newestTimestamp: sortedQueue[sortedQueue.length - 1].timestamp
  };
};

/**
 * 
 */
export const getNetworkErrorMessage = (error: any): string => {
  if (!isOnline()) {
    return '';
  }
  
  if (error instanceof TypeError && error.message.includes('fetch')) {
    return '';
  }
  
  if (error.timeout) {
    return '';
  }
  
  return '';
};

/**
 * 
 * 
 *  API 
 */
export const offlineFallbackRequest = async <T>(
  endpoint: string,
  method: string,
  data: any,
  options: {
    timeout?: number; // 
    retryCount?: number; // 
    retryDelay?: number; // 
    allowOfflineQueue?: boolean; // 
  } = {}
): Promise<T> => {
  const {
    timeout = 30000,
    retryCount = 2,
    retryDelay = 1000,
    allowOfflineQueue = true
  } = options;
  
  // 
  if (!isOnline()) {
    if (allowOfflineQueue) {
      const queueId = addToOfflineQueue(endpoint, method, data);
      
      // 
      throw new Error(`OFFLINE_QUEUED:${queueId}`);
    } else {
      throw new Error('OFFLINE_ERROR');
    }
  }
  
  // 
  let lastError: any;
  
  for (let attempt = 0; attempt <= retryCount; attempt++) {
    try {
      // 
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), timeout);
      
      const response = await fetch(endpoint, {
        method,
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(data),
        signal: controller.signal
      });
      
      clearTimeout(timeoutId);
      
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(
          JSON.stringify({
            status: response.status,
            statusText: response.statusText,
            data: errorData
          })
        );
      }
      
      return await response.json();
    } catch (error) {
      lastError = error;
      
      // AbortController 
      if (error && typeof error === 'object' && 'name' in error && error.name === 'AbortError') {
        lastError = { ...(error as Error), timeout: true };
      }
      
      // 
      if (attempt < retryCount) {
        await new Promise(resolve => setTimeout(resolve, retryDelay));
        continue;
      }
      
      // 
      if (allowOfflineQueue) {
        // 
        const queueId = addToOfflineQueue(endpoint, method, data);
        throw new Error(`REQUEST_FAILED_QUEUED:${queueId}`);
      } else {
        throw lastError;
      }
    }
  }
  
  // TypeScript 
  throw lastError;
};