import React, { useState, useEffect } from 'react';
import { AttenderApplicationData } from '../../../types/attender';
import { Save, CheckCircle, AlertCircle, WifiOff, RotateCw } from 'lucide-react';
import { isOnline } from '../../../utils/networkUtils';

interface DraftSaverProps {
  formData: Partial<AttenderApplicationData>;
  onSave: () => Promise<void>;
}

/**
 * 
 * 
 */
const DraftSaver: React.FC<DraftSaverProps> = ({ formData, onSave }) => {
  const [isSaving, setIsSaving] = useState(false);
  const [lastSaved, setLastSaved] = useState<Date | null>(null);
  const [saveSuccess, setSaveSuccess] = useState(false);
  const [saveCount, setSaveCount] = useState(0);
  const [saveError, setSaveError] = useState<string | null>(null);
  const [isOffline, setIsOffline] = useState(!isOnline());

  // 
  useEffect(() => {
    const handleOnline = () => setIsOffline(false);
    const handleOffline = () => setIsOffline(true);
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  // 60
  useEffect(() => {
    const timer = setInterval(() => {
      handleAutoSave();
    }, 60000); // 60

    return () => clearInterval(timer);
  }, [formData]);

  // 
  const handleManualSave = async () => {
    try {
      setIsSaving(true);
      setSaveError(null);
      
      await onSave();
      
      setLastSaved(new Date());
      setSaveSuccess(true);
      setSaveCount(prev => prev + 1);
      
      // 3
      setTimeout(() => {
        setSaveSuccess(false);
      }, 3000);
    } catch (error) {
      console.error(':', error);
      setSaveError('');
    } finally {
      setIsSaving(false);
    }
  };

  // 
  const handleAutoSave = async () => {
    // 
    if (isOffline || isSaving) return;
    
    try {
      // 
      if (Object.keys(formData).length === 0) return;
      
      setIsSaving(true);
      await onSave();
      setLastSaved(new Date());
      setSaveCount(prev => prev + 1);
    } catch (error) {
      console.error(':', error);
      // 
    } finally {
      setIsSaving(false);
    }
  };

  // 
  const handleRetry = () => {
    setSaveError(null);
    handleManualSave();
  };

  // 
  const formattedLastSaved = lastSaved 
    ? `${lastSaved.toLocaleDateString('ja-JP')} ${lastSaved.toLocaleTimeString('ja-JP')}`
    : '';

  return (
    <div className="bg-white rounded-md p-3 border border-gray-200 shadow-sm">
      <div className="flex items-center justify-between">
        <div>
          <h3 className="text-sm font-medium"></h3>
          <p className="text-xs text-gray-500">
            : {formattedLastSaved}
            {saveCount > 0 && ` (${saveCount})`}
          </p>
        </div>
        
        <div className="flex gap-2">
          <button
            type="button"
            onClick={handleManualSave}
            disabled={isSaving}
            className={`flex items-center px-3 py-1 rounded-md text-sm ${
              saveSuccess
                ? 'bg-green-100 text-green-700'
                : saveError
                  ? 'bg-red-100 text-red-700'
                  : isOffline
                    ? 'bg-amber-100 text-amber-700'
                    : isSaving
                      ? 'bg-gray-100 text-gray-400 cursor-not-allowed'
                      : 'bg-blue-100 text-blue-700 hover:bg-blue-200'
            }`}
          >
            {saveSuccess ? (
              <>
                <CheckCircle className="w-4 h-4 mr-1" />
                
              </>
            ) : saveError ? (
              <>
                <AlertCircle className="w-4 h-4 mr-1" />
                
              </>
            ) : isOffline ? (
              <>
                <WifiOff className="w-4 h-4 mr-1" />
                
              </>
            ) : isSaving ? (
              <>
                <Save className="w-4 h-4 mr-1 animate-pulse" />
                ...
              </>
            ) : (
              <>
                <Save className="w-4 h-4 mr-1" />
                
              </>
            )}
          </button>
          
          {/*  */}
          {saveError && (
            <button
              type="button"
              onClick={handleRetry}
              className="flex items-center px-2 py-1 rounded-md text-sm bg-blue-100 text-blue-700 hover:bg-blue-200"
            >
              <RotateCw className="w-4 h-4" />
            </button>
          )}
        </div>
      </div>
      
      {/*  */}
      <div className="mt-2">
        {saveError ? (
          <p className="text-xs text-red-600">
            {saveError}
          </p>
        ) : isOffline ? (
          <p className="text-xs text-amber-600">
            
          </p>
        ) : (
          <p className="text-xs text-gray-500">
            1
          </p>
        )}
      </div>
    </div>
  );
};

export default DraftSaver;